  for (let i = 0; i < NUM; i++) {
    arr1[i] = {};
    arr1[i].id = `id-${i}`;
    arr1[i].name = "豬腸粉";
    arr1[i].desc = "好吃的豬腸粉";
    arr1[i].nice = 90;
    arr1[i].sell = 92;
    arr1[i].price = 23;
    arr1[i].count = 0;
  }
  return arr1;
}
